  left: -3vw;
}

#Player4 {
  top: -3vw;
  left: 4vw;
}

#Player5 {
  top: -3vw;
  left: 15.5vw;
}

#Player6 {
  top: -3vw;
  left: 27vw;
}

#Player7 {
  top: 3vw;
  left: 34vw;
}

#Player8 {
